import "./App.css";
import { useState } from "react";
import {
  Choose,
  When,
  Otherwise,
  If,
} from "babel-plugin-jsx-control-statements";

function App() {
  const [isActive, setIsActive] = useState("");
  return (
    <div className="App">
      <header className="App-header">
        <b>My App</b>
        <hr style={{ width: "100%" }} />
        <button
          onClick={() => {
            setIsActive("a");
          }}
        >
          a
        </button>
        <button
          onClick={() => {
            setIsActive("b");
          }}
        >
          b
        </button>
        <button
          onClick={() => {
            setIsActive("c");
          }}
        >
          c
        </button>
        <button
          onClick={() => {
            setIsActive("d");
          }}
        >
          d
        </button>
        <p display-if={isActive}>Hello</p>
        <p>{isActive && "Hello"}</p>
        <h1 display-if={isActive}>
          <p>Hello</p>
          <pre>
Tell About Yourself. <br />
How old are you.
          </pre>
        </h1>

        <Choose>
          <When condition={isActive === "a"}>
            <p>a</p>
          </When>
          <When condition={isActive === "b"}>
            <p>b</p>
          </When>
          <When condition={isActive === "c"}>
            <p>c</p>
          </When>
          <When condition={isActive === "d"}>
            <p>d</p>
          </When>
          <Otherwise>
            <p>Else</p>
          </Otherwise>
        </Choose>

        <button
          onClick={() => {
            setIsActive("");
          }}
        >
          e
        </button>

        <If condition={isActive === "e"}>
          <p>e</p>
        </If>

        
      </header>
    </div>
  );
}

export default App;
